import React, { Component } from "react";
import PropTypes from "prop-types";
import { Alert } from "reactstrap";

// Core Components
import Navigator  from "../../components/Navigator/Navigator.js";
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";

import Mobile  from "./EquipmentType/Mobile.js";
import Desktop from "./EquipmentType/Desktop.js";

// function
import highestRarity from "../../components/highestRarity.js";

const propTypes = {
  navigate: PropTypes.any,
  azurapi: PropTypes.any,
  type: PropTypes.string
};

export default class EquipmentType extends Component
{
  render()
  {
    const { navigate, azurapi, type } = this.props;
    const AZURAPI = highestRarity(azurapi);

    const LINK_TYPE = {
      "destroyer_guns": "Destroyer Guns",
      "light_cruiser_guns": "Light Cruiser Guns",
      "heavy_cruiser_guns": "Heavy Cruiser Guns",
      "large_cruiser_guns": "Large Cruiser Guns",
      "battleship_guns": "Battleship Guns",
      "ship_torpedoes": "Ship Torpedoes",
      "submarine_torpedoes": "Submarine Torpedoes",
      "fighter_planes": "Fighter Planes",
      "dive_bomber_planes": "Dive Bomber Planes",
      "torpedo_bomber_planes": "Torpedo Bomber Planes",
      "seaplanes": "Seaplanes",
      "anti-air_guns": "Anti-Air Guns",
      "anti-submarine_equipment": "Anti-Submarine Equipment",
      "auxiliary_equipment": "Auxiliary Equipment"
    };

    const EQUIPMENT_TYPE = LINK_TYPE[type];
    let equipment_list = [];
    AZURAPI.forEach((gear) => { if (gear.category === EQUIPMENT_TYPE) equipment_list.push(gear); });

    equipment_list.sort((a, b) => b.highest_rarity - a.highest_rarity);

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <div className="main text-center align-middle">
            <div className="content">
              <Navigator equipment path={EQUIPMENT_TYPE} />
              <Alert color="warning">
                This page is under Development!
              </Alert>
              <Mobile navigate={navigate} equipmentList={equipment_list} />
              <Desktop navigate={navigate} type={type} equipmentList={equipment_list} />
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

EquipmentType.propTypes = propTypes;
