import React, { Component } from "react";
import PropTypes from "prop-types";
import InfiniteScroll from "react-infinite-scroller";
import {
  Alert,
  Button,
  InputGroup,
  InputGroupText,
  Input
} from "reactstrap";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";
import rarityGet  from "../../components/rarityGet.js";

import ShipCard   from "./ShipList/ShipCard.js";
import ShipModal  from "./ShipList/ShipModal.js";
import shipSearch from "./ShipList/shipSearch.js";
import shipFilter from "./ShipList/shipFilter.js";

const propTypes = {
  azurapi: PropTypes.any,
  shipBanner: PropTypes.any
};

export default class ShipList extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      azurapi: [],
      search: "",
      modal: false,
      modalData: [],
      focused: "",
      sortName: false,
      propsUpdate: false,
      remnant: [],
      hasMore: false
    };
    this.timer = null;
  };

  componentDidMount()
  {
    const { azurapi } = this.props;
    const { propsUpdate } = this.state;

    if (!propsUpdate && azurapi.length)
    {
      const AZURAPI = [...azurapi];
      const REMNANT = AZURAPI.splice(100);
      this.setState({
        azurapi: AZURAPI,
        remnant: REMNANT,
        propsUpdate: true,
        hasMore: true
      });
    };
  };

  componentDidUpdate(prevProps, prevState)
  {
    const { azurapi } = this.props;
    const { propsUpdate, search } = this.state;

    if (!propsUpdate && prevState.azurapi !== azurapi)
    {
      const AZURAPI = [...azurapi];
      const REMNANT = AZURAPI.splice(100);
      this.setState({
        azurapi: AZURAPI,
        remnant: REMNANT,
        propsUpdate: true,
        hasMore: true
      });
    };

    if (propsUpdate && prevState.search !== search)
      this.searchResult();
  };

  reset = () =>
    this.setState({
      azurapi: this.props.azurapi,
      modalData: []
    });

  modalOpen = () =>
    this.setState({ modal: true });

  modalClose = (tag) =>
    this.setState({
      azurapi: shipFilter(this.props.azurapi, tag),
      modal: false,
      modalData: tag
    });

  onFocus = () => this.setState({ focused: "input-group-focus" });
  onBlur = () => this.setState({ focused: "" });

  search = (event) =>
  {
    const { timer } = this;
    clearTimeout(timer);
    timer = setTimeout(() => this.setState({ search: event.target.value }), 1000);
  };

  searchResult = () =>
  {
    const { search } = this.state;
    const { azurapi } = this.props;

    if (search)
    {
      const SEARCH = shipSearch(search, azurapi);
      this.setState({ azurapi: SEARCH, hasMore: false });
    }
    else
    {
      const AZURAPI = [...azurapi];
      const REMNANT = AZURAPI.splice(100);
      this.setState({ azurapi: AZURAPI, remnant: REMNANT, hasMore: true });
    };
  };

  sortByRarity = () =>
  {
    const { azurapi, remnant } = this.state;
    const AZURAPI = [...azurapi, ...remnant].sort((a, b) => rarityGet(b.rarity, 2) - rarityGet(a.rarity, 2));
    const REMNANT = AZURAPI.splice(100);
    this.setState({ azurapi: AZURAPI, remnant: REMNANT, hasMore: true });
  };

  sortByName = () =>
  {
    const { sortName, azurapi, remnant } = this.state;

    if (sortName)
    {
      const AZURAPI = [...azurapi, ...remnant].sort((a, b) => a.names.en.localeCompare(b.names.en));
      const REMNANT = AZURAPI.splice(100);
      this.setState({ azurapi: AZURAPI, remnant: REMNANT, sortName: false, hasMore: true });
    }
    else
    {
      const AZURAPI = [...azurapi, ...remnant].sort((a, b) => b.names.en.localeCompare(a.names.en));
      const REMNANT = AZURAPI.splice(100);
      this.setState({ azurapi: AZURAPI, remnant: REMNANT, sortName: true, hasMore: true });
    };
  };

  loadMore = () =>
  {
    const { azurapi, remnant } = this.state;
    const DEFAULT_AZURAPI = this.props.azurapi;
    console.log({ azurapi, remnant, DEFAULT_AZURAPI });

    const AZURAPI = remnant;
    const REMNANT = AZURAPI.splice(100);
    const CONTAINER = azurapi.concat(AZURAPI);

    this.setState({ azurapi: CONTAINER, remnant: REMNANT });

    if (CONTAINER.length >= DEFAULT_AZURAPI.length)
      this.setState({ hasMore: false });
  };

  render()
  {
    console.log("render");
    const {
      modalClose,
      modalOpen,
      onFocus,
      onBlur,
      search,
      reset,
      sortByRarity,
      sortByName,
      loadMore
    } = this;
    const { modalData, modal, focused, azurapi, sortName, hasMore } = this.state;
    const BANNER = this.props.shipBanner;
    const SORT_BY_NAME = sortName ? "A-Z" : "Z-A";
    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <Alert color="warning" className="text-center align-middle">
            This page is under Development!
          </Alert>
          <div className="main text-center align-middle shipheader">
            <ShipModal data={modalData} close={modalClose} modal={modal}/>
            <div className="content">
              <h1>Ship List</h1>
              <Button outline color="danger" className="btn-icon" onClick={reset}>
                <i className="tim-icons icon-refresh-02"/>
              </Button>
              <Button outline color="success" className="btn-icon" onClick={modalOpen}>
                <i className="tim-icons icon-tag"/>
              </Button>
              <Button outline color="success" className="btn-icon" onClick={sortByName}>
                {SORT_BY_NAME}
              </Button>
              <Button outline color="success" className="btn-icon" onClick={sortByRarity}>
                <i className="tim-icons icon-shape-star"/>
              </Button>
              <InputGroup className={focused}>
                <div className="input-group-prepend">
                  <InputGroupText><i className="tim-icons icon-zoom-split"/></InputGroupText>
                </div>
                <Input
                  type="search"
                  placeholder="Search"
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onChange={search}
                />
              </InputGroup>
              <InfiniteScroll
                className="containerCS"
                threshold={0}
                pageStart={0}
                loadMore={loadMore}
                hasMore={hasMore}
                loader={<div key={0} className="text-center">loading data ...</div>}
              >
                {azurapi.map((ship) =>
                {
                  const SHIP_BANNER = BANNER.find(banner => banner.id === ship.id);
                  return (<ShipCard key={ship.id} ship={ship} banner={SHIP_BANNER} />);
                })}
              </InfiniteScroll>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

ShipList.propTypes = propTypes;
